@import "../scss/01-variables";

// Timeline styling
.tm {
    min-height: 100vh;
    position: relative;
    padding-top: 3rem;

    &__line {
        position: absolute;
        width: 2px;
        top: 0;
        bottom: 0;
        left: 100%;
        transform: translateX(-3rem);
        background-color: $lineColor;
        z-index: -1;
    }
}
